












































import {
  Component, Ref, Vue, Watch,
} from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import {
  IInputInfo, IKind, IPage, IContent, IInformationInput,
} from '@/types/interface';
import * as commonTypes from '@/types/common';
import AdvertisingBannerList from '../../components/AdvertisingBannerList.vue';
import InputDropdown from '../../components/InputDropdown.vue';
import InputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    AdvertisingBannerList,
    InputDropdown,
    InputFormPanel,
  },
})

export default class InformationPage extends Vue {
  @Ref('inputForm') readonly inputForm!: InputFormPanel;
  @State('kindMenu', { namespace: 'testKind' }) stateKindMenu!: IKind[];
  @State('typePage', { namespace: 'testKind' }) stateTypePage!: IPage[];
  @State('typeContent', { namespace: 'testKind' }) stateTypeContent!: IContent[];
  @Action('getKindMenu', { namespace: 'testKind' }) getKindMenu!: (params: IInformationInput) => void;
  @Action('getKindPageList', { namespace: 'testKind' }) getKindPageList!: (params: IInformationInput) => Promise<void>;
  @Action('getKindPage', { namespace: 'testKind' }) getKindPage!: (params: IInformationInput) => void;

  advertisingList = [
    {
      id: '1',
      imgUrl: '',
    },
    {
      id: '2',
      imgUrl: '',
    },
    {
      id: '3',
      imgUrl: '',
    },
    {
      id: '4',
      imgUrl: '',
    },
  ];

  typename = '';
  kindname = '';
  selectTheme = '';

  isExpand = true;

  inputList = [commonTypes.INPUT_NAME, commonTypes.INPUT_PHONE, commonTypes.INPUT_EMAIL,
    commonTypes.INPUT_FULL_ADDRESS, commonTypes.INPUT_CK_CLASS, commonTypes.INPUT_CK_CONSULT];

  created() {
    this.typename = JSON.parse(JSON.stringify(this.$route.query.typename));
    this.getKindMenu({ typename: this.typename });
  }

  @Watch('selectTheme')
  onThemeChange(nVal: string) {
    this.getKindPage(
      {
        publicname: 'jobs',
        typename: this.typename,
        kindname: this.kindname,
        pagename: nVal,
      },
    );
  }

  select(kindname: string) {
    this.kindname = kindname;
    const temp = {
      typename: this.typename,
      kindname,
    };
    this.getKindPageList(temp)
      .then(() => {
        this.selectTheme = this.stateTypePage[0].pagename;
      });
  }

  send() {
    const info: IInputInfo | undefined = this.inputForm.getInput();
    console.log(info);
  }
}
